import axios from 'axios'
import { variable } from '../configs/variable'

export const getOrders = async (id = null, status = null) => {
    try {

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        const endpoint = variable.env === 'local' ? variable.endpointLocal : variable.endpointProd

        let url = null

        if (id) {
            url = `${endpoint}/api/orders/${id}`
        } else if (status) {
            url = `${endpoint}/api/orders?status=${status}`
        } else {
            url = `${endpoint}/api/orders`
        }

        const { data } = await axios.get(`${url}`, config)

        return {status: true, data}
    } catch (error) {
        const message = error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        
        return {status: false, error: message}
    }
}

export const createOrders = async (body) => {
    try {

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        const endpoint = variable.env === 'local' ? variable.endpointLocal : variable.endpointProd

        const { data } = await axios.post(`${endpoint}/api/orders`, body, config)

        return {status: true, data}
    } catch (error) {
        const message = error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        
        return {status: false, error: message}
    }
}

