import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableRow,
  CButtonGroup,
  CButton,
  CTableHead,
  CTableHeaderCell,
} from "@coreui/react";
import { useParams, useHistory } from "react-router-dom";

import Loader from "../../components/Loader";
import Message from "../../components/Message";
import { Authentification } from "../../components/index";

import { getOrders } from "../../provider/orderFetch";

const DetailCommande = () => {
  /**Params Route */
  let { id } = useParams();
  let history = useHistory();

  /** State */
  const [order, setOrder] = useState(null);
  const [error, setError] = useState(null);

  /** Run once, when the component mounts */
  useEffect(() => {
    console.log("Run once, when the component mounts");

    async function fetch() {
      const resp = await getOrders(id);

      resp?.status ? setOrder(resp?.data) : setError(resp?.error);
    }

    fetch();
  }, [id]);

  const back = () => {
    history.push(`/administrateurs`);
  };

  if (order === null) {
    return <Loader />;
  }

  if (error) {
    return <Message> {error}</Message>;
  }

  return (
    <>
      <Authentification />
      <CRow>
        <CCol xs={12}>
          <CCard className="mb-4">
            <CCardHeader>
              <CRow>
                <CCol sm={5}>
                  <strong>Commande {id}</strong>
                </CCol>
                <CCol sm={7}>
                  <CButtonGroup className="float-end me-3">
                    <CButton
                      color="outline-secondary"
                      className="mx-0"
                      onClick={() => back()}
                    >
                      Retour
                    </CButton>
                  </CButtonGroup>
                </CCol>
              </CRow>
            </CCardHeader>
            <CCardBody>
              <CTable>
                <CTableBody>
                  <CTableRow>
                    <CTableDataCell>Numero</CTableDataCell>
                    <CTableDataCell>{order.numero}</CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>firstName</CTableDataCell>
                    <CTableDataCell>{order.firstName}</CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>lastName</CTableDataCell>
                    <CTableDataCell>{order.lastName}</CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>email</CTableDataCell>
                    <CTableDataCell>{order.email}</CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>Status</CTableDataCell>
                    <CTableDataCell>{order.status}</CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>Creation</CTableDataCell>
                    <CTableDataCell>{order.createdAt}</CTableDataCell>
                  </CTableRow>
                </CTableBody>
              </CTable>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
      <CRow>
        <CCol xs={12}>
          <CCard className="mb-4">
            <CCardHeader>
              <CRow>
                <CCol sm={5}>
                  <strong>Items</strong>
                </CCol>
              </CRow>
            </CCardHeader>
            <CCardBody>
              <CTable>
                <CTableHead>
                  <CTableRow>
                    <CTableHeaderCell scope="col">Name</CTableHeaderCell>
                    <CTableHeaderCell scope="col">Price</CTableHeaderCell>
                    <CTableHeaderCell scope="col">Quantity</CTableHeaderCell>
                  </CTableRow>
                </CTableHead>
                <CTableBody>
                  {order.items.length > 0 ? (
                    order.items.map((item) => (
                      <CTableRow key={item._id}>
                        <CTableDataCell>{item.name}</CTableDataCell>
                        <CTableDataCell>{item.price}</CTableDataCell>
                        <CTableDataCell>{item.quantity}</CTableDataCell>
                      </CTableRow>
                    ))
                  ) : (
                    <h3>{"NO Items"}</h3>
                  )}
                </CTableBody>
              </CTable>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
      <CRow>
        <CCol xs={12}>
          <CCard className="mb-4">
            <CCardHeader>
              <CRow>
                <CCol sm={5}>
                  <strong>Address</strong>
                </CCol>
              </CRow>
            </CCardHeader>
            <CCardBody>
              <CTable>
                <CTableBody>
                  <CTableRow>
                    <CTableDataCell>address</CTableDataCell>
                    <CTableDataCell>
                      {order.shippingAddress.address}
                    </CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>city</CTableDataCell>
                    <CTableDataCell>
                      {order.shippingAddress.city}
                    </CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>zipCode</CTableDataCell>
                    <CTableDataCell>
                      {order.shippingAddress.zipCode}
                    </CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>country</CTableDataCell>
                    <CTableDataCell>
                      {order.shippingAddress.country}
                    </CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>latitude</CTableDataCell>
                    <CTableDataCell>
                      {order.shippingAddress.latitude}
                    </CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>longitude</CTableDataCell>
                    <CTableDataCell>
                      {order.shippingAddress.longitude}
                    </CTableDataCell>
                  </CTableRow>
                </CTableBody>
              </CTable>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </>
  );
};

export default DetailCommande;
